@use 'base/variables' as v;
@use 'base/mixins' as m;

.testimoniales {
    background-color: v.$primario;
    color: v.$blanco;
    padding: 5rem 0;

    &__heading {
        font-size: 6rem;
    }

    &__grid {
        @include m.tablet { 
            @include m.grid(5, 2rem);
        }
    }
}

.testimonial {
    background-color: v.$blanco;
    padding: 2rem;
    margin-bottom: 2rem;
    border-radius: 1rem;
    color: v.$negro;
    transition-property: transform;
    transition-duration: 300ms;

    &:hover {
        transform: scale(1.05);
    }

    &:last-of-type {
        margin-bottom: 0;
    }

    &__header {
        display: flex;
        gap: 2rem;
        align-items: center;
    }

    &__imagen {
        flex-basis: 10rem;
        border-radius: 50%;
        overflow: hidden;
    }

    &__autor {
        color: v.$negro;
        font-weight: bold;
        font-size: 3rem;
        margin: .5rem;
    }

    &__texto {
        font-size: 2rem;
        line-height: 1.5;
        margin: 2rem 0 0 0;
        padding-left: 4rem;
        position: relative;

        &::before {
            content: ' " ';
            font-size: 10rem;
            position: absolute;
            left: 0;
            top: -4rem;
            opacity: .2;
            font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
        }
    }

    @include m.tablet { 
        margin-bottom: 0;
         
        &:nth-child(1) {
            grid-column: 1 / 4;
        }
        &:nth-child(3) {
            grid-column: 5 / 6;
            grid-row: 1 / 3;
        }
        &:nth-child(4) {
            grid-column: 1 / 3;
        }
        &:nth-child(5) {
            grid-column: 3 / 5;
        }
    }
}