/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Oswald", sans-serif;
  font-size: 1.6rem;
  background-color: #F9FAFC;
}

p {
  color: #000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
  font-family: "Gemunu Libre", sans-serif;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

.header {
  padding: 1rem 0 2rem 0;
  background-color: #F9FAFC;
}
@media (min-width: 768px) {
  .header {
    padding: 1rem 0;
    background-color: #eceeef;
  }
}
.header__barra {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
}
@media (min-width: 768px) {
  .header__barra {
    flex-direction: row;
    padding: 0 3rem 0 3rem;
    gap: 0;
  }
}
@media (min-width: 768px) {
  .header__logo {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
}
.header__imagen {
  max-width: 30rem;
}
@media (min-width: 768px) {
  .header__imagen {
    max-width: 10rem;
  }
}
.header__texto {
  text-align: center;
  font-size: 5rem;
  margin: 0;
}
@media (min-width: 768px) {
  .header__texto {
    font-size: 3rem;
  }
}

.navegacion {
  width: 100%;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .navegacion {
    margin-bottom: 0;
  }
}
.navegacion__principal {
  display: grid;
  gap: 0;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .navegacion__principal {
    display: flex;
    justify-content: right;
  }
}
.navegacion__enlace {
  color: #F9FAFC;
  font-size: 2.5rem;
  padding: 2rem;
  background-color: #0a2472;
  text-align: center;
}
.navegacion__enlace--claro {
  background-color: #15b0f2;
}
@media (min-width: 768px) {
  .navegacion__enlace {
    background-color: #eceeef;
    color: #0a2472;
    margin-right: 2rem;
    border-radius: 1.5rem;
    transition-property: transform;
    transition-duration: 300ms;
  }
  .navegacion__enlace:last-of-type {
    margin-right: 0;
  }
}
@media (min-width: 768px) {
  .navegacion__enlace:hover {
    background-color: #15b0f2;
    color: #F9FAFC;
    transform: scale(1.12) rotate(3deg);
  }
}
@media (min-width: 768px) {
  .navegacion__enlace--boton {
    background-color: #0a2472;
    color: #F9FAFC;
  }
}
@media (min-width: 768px) {
  .navegacion__enlace--boton:hover {
    background-color: black;
    color: #F9FAFC;
    transform: scale(1.12) rotate(3deg);
  }
}

.hero {
  min-height: 45rem;
  background-image: url(../img/hero.jpeg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom bottom;
}
@media (min-width: 1024px) {
  .hero {
    min-height: 55rem;
    background-position: left top;
    background-image: url(../img/heroimg.jpeg);
  }
}
.hero__texto {
  display: flex;
  flex-direction: column;
  padding-top: 1.5rem;
}
@media (min-width: 768px) {
  .hero__texto {
    padding-top: 4rem;
  }
}
.hero__titulo, .hero__descripcion {
  margin: 0.5rem 0;
  text-align: left;
}
@media (min-width: 768px) {
  .hero__titulo, .hero__descripcion {
    text-align: right;
  }
}
.hero__titulo {
  font-size: 3.8rem;
  padding-bottom: 24rem;
  text-align: center;
}
@media (min-width: 768px) {
  .hero__titulo {
    font-size: 7rem;
    padding-bottom: 26rem;
    padding-left: 10rem;
    text-align: right;
  }
}
.hero__descripcion {
  font-size: 2.2rem;
}
@media (min-width: 768px) {
  .hero__descripcion {
    font-size: 2.5rem;
  }
}

.ventajas__titulo {
  font-size: 6rem;
  text-align: center;
  color: #0a2472;
  border-top: 1rem solid #0a2472;
  padding-top: 2rem;
}
@media (min-width: 768px) {
  .ventajas__grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.ventaja__info {
  padding-top: 3rem;
}
.ventaja__titulo {
  color: #0a2472;
}
.ventaja__titulo, .ventaja__descripcion {
  text-align: center;
}
@media (min-width: 768px) {
  .ventaja__ilustracion {
    object-fit: cover;
    height: 18rem;
    margin: 0 auto;
  }
}

.nosotros {
  background-image: linear-gradient(to top, transparent 50%, #0a2472 0%), url(../img/nosotros.jpeg);
  background-position: center;
  background-repeat: repeat, no-repeat;
  background-size: 100%, 170rem;
}
@media (min-width: 768px) {
  .nosotros {
    background-image: linear-gradient(to left, transparent 50%, #0a2472 0%), url(../img/nosotros.jpeg);
    background-size: 100%, 210rem;
  }
}
.nosotros__grid {
  display: grid;
  grid-template-rows: 2, 40rem;
  row-gap: 5rem;
}
@media (min-width: 768px) {
  .nosotros__grid {
    grid-template-rows: unset;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 5rem;
    padding: 5rem 0;
  }
}
.nosotros__texto {
  grid-row: 2/3;
  color: #F9FAFC;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding-bottom: 60rem;
}
@media (min-width: 768px) {
  .nosotros__texto {
    padding-bottom: 0;
  }
}
.nosotros__titulo, .nosotros__descripcion {
  color: #F9FAFC;
}
.nosotros__titulo {
  font-size: 6rem;
}
.nosotros__titulo--span {
  font-size: 7.5rem;
}

.testimoniales {
  background-color: #0a2472;
  color: #F9FAFC;
  padding: 5rem 0;
}
.testimoniales__heading {
  font-size: 6rem;
}
@media (min-width: 768px) {
  .testimoniales__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(5, 1fr);
  }
}

.testimonial {
  background-color: #F9FAFC;
  padding: 2rem;
  margin-bottom: 2rem;
  border-radius: 1rem;
  color: #000;
  transition-property: transform;
  transition-duration: 300ms;
}
.testimonial:hover {
  transform: scale(1.05);
}
.testimonial:last-of-type {
  margin-bottom: 0;
}
.testimonial__header {
  display: flex;
  gap: 2rem;
  align-items: center;
}
.testimonial__imagen {
  flex-basis: 10rem;
  border-radius: 50%;
  overflow: hidden;
}
.testimonial__autor {
  color: #000;
  font-weight: bold;
  font-size: 3rem;
  margin: 0.5rem;
}
.testimonial__texto {
  font-size: 2rem;
  line-height: 1.5;
  margin: 2rem 0 0 0;
  padding-left: 4rem;
  position: relative;
}
.testimonial__texto::before {
  content: ' " ';
  font-size: 10rem;
  position: absolute;
  left: 0;
  top: -4rem;
  opacity: 0.2;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif;
}
@media (min-width: 768px) {
  .testimonial {
    margin-bottom: 0;
  }
  .testimonial:nth-child(1) {
    grid-column: 1/4;
  }
  .testimonial:nth-child(3) {
    grid-column: 5/6;
    grid-row: 1/3;
  }
  .testimonial:nth-child(4) {
    grid-column: 1/3;
  }
  .testimonial:nth-child(5) {
    grid-column: 3/5;
  }
}

.cotizador {
  padding: 5rem 0;
}
.cotizador__borde-azul {
  border: 0.5rem solid #0a2472;
}
.cotizador__heading {
  font-size: 6rem;
}
.cotizador__formulario {
  padding: 3rem;
}
.cotizador__campo {
  display: flex;
  align-items: center;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .cotizador__3campos {
    display: flex;
    justify-content: space-evenly;
  }
}
.cotizador__label, .cotizador__select {
  font-size: 2.5rem;
}
@media (min-width: 768px) {
  .cotizador__label, .cotizador__select {
    font-size: 3.5rem;
  }
}
.cotizador__label {
  padding-right: 3rem;
  padding-bottom: 1rem;
}
.cotizador__label--garantia {
  font-size: 2.5rem;
}
@media (min-width: 768px) {
  .cotizador__label--garantia {
    padding: 4rem;
  }
}
.cotizador__boton {
  background-color: #0a2472;
  color: #F9FAFC;
  border-radius: 1.5rem;
  transition-property: transform;
  transition-duration: 300ms;
  padding: 1rem;
  text-align: center;
  border: none;
  font-size: 2.5rem;
  width: 100%;
}
.cotizador__boton:hover {
  transform: scale(1.05);
  color: #0a2472;
  background-color: #a6e1fa;
}
.cotizador button:hover {
  transform: scale(1);
}
.cotizador__fieldset {
  padding-bottom: 4rem;
}
.cotizador__spinner {
  display: none;
}

#cargando {
  text-align: center;
}

#cargando img {
  display: none;
  margin: 0 auto;
}

#resultado {
  margin-bottom: 40px;
}

#resultado div {
  border: 1px solid #0a2472;
  text-align: center;
  padding: 0 0 20px 0;
}

#resultado div p {
  margin-bottom: 0;
}

#resultado div p.header {
  background-color: #0a2472;
  margin-bottom: 10px;
  color: white;
  padding: 10px;
  text-transform: uppercase;
  font-weight: bold;
}

.spinner {
  margin: 5rem auto;
  width: 50px;
  height: 40px;
  text-align: center;
  font-size: 10px;
}

.spinner > div {
  background-color: #0a2472;
  height: 100%;
  width: 6px;
  display: inline-block;
  -webkit-animation: sk-stretchdelay 1.2s infinite ease-in-out;
  animation: sk-stretchdelay 1.2s infinite ease-in-out;
}

.spinner .rect2 {
  -webkit-animation-delay: -1.1s;
  animation-delay: -1.1s;
}

.spinner .rect3 {
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
}

.spinner .rect4 {
  -webkit-animation-delay: -0.9s;
  animation-delay: -0.9s;
}

.spinner .rect5 {
  -webkit-animation-delay: -0.8s;
  animation-delay: -0.8s;
}

@-webkit-keyframes sk-stretchdelay {
  0%, 40%, 100% {
    -webkit-transform: scaleY(0.4);
  }
  20% {
    -webkit-transform: scaleY(1);
  }
}
@keyframes sk-stretchdelay {
  0%, 40%, 100% {
    transform: scaleY(0.4);
    -webkit-transform: scaleY(0.4);
  }
  20% {
    transform: scaleY(1);
    -webkit-transform: scaleY(1);
  }
}
.mensaje {
  margin-top: 2rem;
}

.error {
  background-color: #d7263d;
  border: 0.3rem solid #d7263d;
  padding: 1rem;
  margin-bottom: 2rem;
  text-align: center;
  color: #F9FAFC;
  border-radius: 1.5rem;
}

.correcto {
  background-color: #1b998b;
  border: 0.3rem solid #1b998b;
  padding: 1rem;
  margin-bottom: 2rem;
  text-align: center;
  color: #F9FAFC;
  border-radius: 1.5rem;
}

.resumen {
  font-size: 2.9rem;
  color: #0a2472;
}

.total {
  font-size: 2.7rem;
  font-weight: bold;
}

.cuadro-resumen {
  border: 3px solid #a6e1fa;
}

.galeria__heading {
  font-size: 6rem;
}
.galeria__grid {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .galeria__grid {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.galeria__img {
  object-fit: cover;
  height: 30rem;
  width: 30rem;
}
@media (min-width: 768px) {
  .galeria__imagen:nth-child(1) {
    grid-row: 1/3;
  }
  .galeria__imagen:nth-child(1) img {
    object-position: center;
    height: 61rem;
  }
  .galeria__imagen:nth-child(2) {
    grid-column: 2/4;
  }
  .galeria__imagen:nth-child(2) img {
    object-position: center;
    width: 61rem;
  }
  .galeria__imagen:nth-child(5) {
    grid-column: 3/5;
  }
  .galeria__imagen:nth-child(5) img {
    object-position: center;
    width: 61rem;
  }
  .galeria__imagen:nth-child(6) {
    grid-column: 1/3;
    grid-row: 3/5;
  }
  .galeria__imagen:nth-child(6) img {
    object-position: center;
    width: 61rem;
    height: 61rem;
  }
  .galeria__imagen:nth-child(8) {
    display: none;
  }
  .galeria__imagen:nth-child(9) {
    grid-row: 3/5;
    grid-column: 4/5;
  }
  .galeria__imagen:nth-child(9) img {
    object-position: center;
    height: 61rem;
  }
}

.footer {
  padding: 0.5rem;
  margin-top: 5rem;
  background-color: #0a2472;
}
.footer__copyright {
  color: #F9FAFC;
}